.base-label {
  line-height: 18px;
  font-size: 14px;
  font-weight: 300;
}
.base-label_left {
  height: 36px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.base-label_left .base-label {
    text-align: right;
}
.base-label_left .base-label.base-label_required:before {
      content: '*';
      position: relative;
      left: 0;
      margin-right: 2px;
}
.base-label_top .base-label {
  margin-left: 15px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.base-label_top .base-label.base-label_required:after {
    content: '*';
    position: relative;
    right: 0;
    margin-left: 2px;
}
