.input-datetime__wrap {
  display: flex;
  width: 100%;
}
.input-datetime__wrap_label-top {
    flex-direction: column;
}
.input-datetime__wrap_label-left {
    flex-direction: row;
}
.input-datetime__input-wrap {
  position: relative;
}
.input-datetime__input {
  border: 1px solid #D0D8DC;
  border-radius: 5px;
  padding: 0 15px;
  font-size: 14px;
  height: 36px;
  width: 100%;
}
.input-datetime__input:focus {
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
}
.input-datetime__input:before {
    font-family: 'cp-fonts';
    content: '';
    position: absolute;
}
.input-datetime__input:disabled {
    background: #F8F8F8;
}
.input-datetime__input_error {
    border-color: #FF9FAF;
}
.input-datetime__icon {
  position: absolute;
  color: #A6A9AA;
  transition: color .2s;
  right: 30px;
  top: 10px;
  cursor: pointer;
}
.input-datetime__icon:before {
    content: '\e912';
    font-family: 'cp-fonts';
    font-size: 15px;
    position: absolute;
}
.input-datetime__icon:hover {
    color: #23292e;
}
.input-datetime__icon_disabled {
    color: #C8CACB;
}
.input-datetime__icon_disabled:hover {
      color: #C8CACB !important;
}
.input-datetime__icon_active {
  color: #23292e;
}
.input-datetime__icon_disabled {
  cursor: default;
}
.input-datetime__icon_disabled:hover {
    color: #A6A9AA;
}
