.kit-forecast__inner {
  width: 100%;
  min-width: 800px;
  min-height: 400px;
}
.kit-forecast__outer {
  position: relative;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 2px 20px 0 rgba(35, 41, 46, 0.2);
  animation: smoothPopup linear 0.2s forwards;
}
.kit-forecast__title {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 20px;
  flex: 0 0 28px;
  margin-left: 40px;
}
.kit-forecast__row-wrap {
  padding: 0 40px;
  min-height: 35px;
  display: flex;
}
.kit-forecast__body {
  padding: 20px 0;
  font-size: 14px;
  width: 800px;
  display: flex;
  flex-direction: column;
}
.kit-forecast__header,
.kit-forecast__row {
  display: flex;
  width: 100%;
  padding: 0 15px;
}
.kit-forecast__header {
  border-bottom: solid 1px #EDF5FA;
  font-weight: 300;
  height: 50px;
}
.kit-forecast__row-wrap:not(:last-child) > .kit-forecast__row {
  border-bottom: solid 1px #EDF5FA;
}
.kit-forecast-header__item,
.kit-forecast-row__cell {
  display: flex;
  align-items: center;
}
