.header-status__wrap {
  margin-left: 20px;
  display: flex;
  align-items: center;
}
.header-status {
  display: inline-flex;
  align-items: center;
  height: 30px;
  padding: 0 10px;
  border-radius: 5px;
  background: #F2F2F2;
  font-size: 14px;
}
.header-status.accent_red {
    background: #FFEDF0;
}
.header-status.accent_green {
    background: #E9F9F0;
}
.header-status.accent_orange {
    background: #FFF3E6;
}
