.input-datetime-picker__wrap {
  position: absolute;
  top: 46px;
  background: white;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  font-family: 'Source Sans Pro';
  color: #23292e;
  z-index: 10;
  left: 50%;
  transform: translateX(-50%);
}
.input-datetime-picker__wrap:before {
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background: transparent;
    border: 5px solid transparent;
    z-index: 9;
}
.input-datetime-picker__wrap_below:before {
    top: -10px;
    border-bottom-color: white;
}
.input-datetime-picker__wrap_on-top:before {
    bottom: -10px;
    border-top-color: white;
}
.input-datetime-picker__head {
  padding: 10px 0 0 0;
  border-bottom: 1px solid #EDF5FA;
}
.input-datetime-picker__body {
  margin: 0 10px 0 10px;
}
.input-datetime-picker__body:last-child {
    padding-bottom: 10px;
}
.input-datetime-picker__month-year-wrap {
  margin: 10px 20px 5px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.input-datetime-picker__prev-month-icon {
  color: #23292e;
  transition: color .2s;
  width: 16px;
  height: 16px;
  position: relative;
  display: block;
}
.input-datetime-picker__prev-month-icon:before {
    content: '\e917';
    font-family: 'cp-fonts';
    cursor: pointer;
    position: relative;
    left: 0;
}
.input-datetime-picker__prev-month-icon:hover {
    color: #2686ED;
}
.input-datetime-picker__next-month-icon {
  color: #23292e;
  transition: color .2s;
  width: 16px;
  height: 16px;
  position: relative;
  display: block;
}
.input-datetime-picker__next-month-icon:before {
    content: '\e918';
    font-family: 'cp-fonts';
    cursor: pointer;
    position: relative;
    right: 0;
}
.input-datetime-picker__next-month-icon:hover {
    color: #2686ED;
}
.input-datetime-picker__month-year {
  font-size: 16px;
  font-weight: 600;
}
.input-datetime-picker__labels-wrap {
  display: flex;
  font-size: 12px;
  font-weight: 300;
  margin: 10px 25px 0 25px;
}
.input-datetime-picker__label {
  width: 28px;
  height: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #919496;
}
.input-datetime-picker__label:not(:last-child) {
    margin-right: 6px;
}
.input-datetime-picker__week {
  display: flex;
  margin: 10px 15px;
}
.input-datetime-picker__day-wrap {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: 1px solid transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color .2s, border-color .2s;
  cursor: pointer;
}
.input-datetime-picker__day-wrap:not(:last-child) {
    margin-right: 6px;
}
.input-datetime-picker__day-wrap:hover {
    color: #2686ED;
    border-color: #D4E7FB;
}
.input-datetime-picker__day {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15px;
  height: 15px;
  font-size: 12px;
}
.input-datetime-picker__day_prev-month {
    color: #C8CACB;
}
.input-datetime-picker__day_next-month {
    color: #C8CACB;
}
.input-datetime-picker__day_checked {
    color: #2686ED;
    background: #E4F0FA;
    border-color: #9CC8F7;
}
.input-datetime-picker__day_disabled {
    color: #C8CACB;
    pointer-events: none;
}
.input-datetime-picker__footer {
  border-top: 1px solid #EDF5FA;
}
.input-datetime-picker__time {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.input-datetime-picker__hours-wrap {
  position: relative;
  margin: 15px 7px 15px 0;
}
.input-datetime-picker__minutes-wrap {
  position: relative;
  margin: 15px 0 15px 7px;
}
.input-datetime-picker__hours {
  border: 1px solid #D0D8DC;
  border-radius: 5px;
  padding: 0 0 0 15px;
  font-size: 14px;
  height: 36px;
  width: 60px;
}
.input-datetime-picker__minutes {
  border: 1px solid #D0D8DC;
  border-radius: 5px;
  padding: 0 0 0 15px;
  font-size: 14px;
  height: 36px;
  width: 60px;
}
.input-datetime-picker__hours-buttons {
  position: absolute;
  right: 10px;
  top: 9px;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.input-datetime-picker__minutes-buttons {
  position: absolute;
  right: 10px;
  top: 9px;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.input-datetime-picker__next-hour-button {
  all: unset;
  color: #A6A9AA;
  transition: color .2s;
  cursor: pointer;
  position: relative;
}
.input-datetime-picker__next-hour-button:hover {
    color: #2686ED;
}
.input-datetime-picker__prev-hour-button {
  all: unset;
  color: #A6A9AA;
  transition: color .2s;
  cursor: pointer;
  position: relative;
}
.input-datetime-picker__prev-hour-button:hover {
    color: #2686ED;
}
.input-datetime-picker__next-minutes-button {
  all: unset;
  color: #A6A9AA;
  transition: color .2s;
  cursor: pointer;
  position: relative;
}
.input-datetime-picker__next-minutes-button:hover {
    color: #2686ED;
}
.input-datetime-picker__prev-minutes-button {
  all: unset;
  color: #A6A9AA;
  transition: color .2s;
  cursor: pointer;
  position: relative;
}
.input-datetime-picker__prev-minutes-button:hover {
    color: #2686ED;
}
.input-datetime-picker__next-hour-icon {
  width: 10px;
  height: 10px;
  display: block;
}
.input-datetime-picker__next-hour-icon:before {
    font-family: 'cp-fonts';
    content: '\e919';
    font-size: 8px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
}
.input-datetime-picker__prev-hour-icon {
  width: 10px;
  height: 10px;
  display: block;
}
.input-datetime-picker__prev-hour-icon:before {
    font-family: 'cp-fonts';
    content: '\e916';
    font-size: 8px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
}
.input-datetime-picker__next-minutes-icon {
  width: 10px;
  height: 10px;
  display: block;
}
.input-datetime-picker__next-minutes-icon:before {
    font-family: 'cp-fonts';
    content: '\e919';
    font-size: 8px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
}
.input-datetime-picker__prev-minutes-icon {
  width: 10px;
  height: 10px;
  display: block;
}
.input-datetime-picker__prev-minutes-icon:before {
    font-family: 'cp-fonts';
    content: '\e916';
    font-size: 8px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
}
