.base-hint__wrap {
  position: relative;
  text-align: left;
}
.base-hint__icon-question {
  margin: 0 10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #EDF5FA;
  border: 1px solid #EDF5FA;
  transition: border-color .2s;
  cursor: pointer;
}
.base-hint__icon-question:hover {
    border: 1px solid #9CC8F7;
}
.base-hint__icon-question:after {
    font-family: 'cp-fonts';
    content: '\e91e';
    position: relative;
    top: 2px;
    left: 2px;
    color: #2686ED;
}
.base-hint__icon-comment {
  color: #2686ED;
  transition: color linear 0.2s;
}
.base-hint__icon-comment:after {
    font-size: 20px;
    font-family: 'cp-fonts';
    content: '\e938';
}
.base-hint__icon-comment:hover {
    color: #23292e;
}
.base-hint__icon-comment_innactive {
  color: #C8CACB;
}
.base-hint__icon-comment_innactive:hover {
    color: #C8CACB;
}
.base-hint__message-wrap {
  z-index: 22;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px;
  color: white;
  background: #778088;
  border-radius: 5px;
}
.base-hint__arrow {
  z-index: 22;
  position: absolute;
  border: 5px solid transparent;
  left: 50%;
  transform: translateX(-50%);
}
.base-hint__arrow_top {
  border-bottom-color: #778088;
  bottom: -10px;
}
.base-hint__arrow_bottom {
  border-top-color: #778088;
  top: -10px;
}
.base-hint__message {
  font-weight: 300;
  font-size: 12px;
  width: max-content;
}
.base-hint__message > ul {
    margin: 5px 0 5px 25px;
    padding-left: 20px;
}
.base-hint__message > ul > li > div {
      display: flex;
      justify-content: space-between;
}
.base-hint__message > ul > li > div > span:first-child {
        margin-right: 10px;
}
.base-hint__message > ul > li > div > span:last-child {
        text-align: right;
}
.base-hint-message__title {
  font-weight: 600;
}
